.todoItem{
    margin: 0 auto;
    border-radius: 5px;
    box-shadow: 1px 2px 5px #B778FF;
    padding: 1rem;
    display: flex;
    align-items: center;
}

.todoItem + .todoItem{
    margin-top: 15px;
}

.content{
    cursor: pointer;
    flex: 1;
    display: flex;
    align-items: center;
}

.content svg{
    font-size: 1.5rem;
    color: #B778FF;
}

.content .text{
    margin-left: 0.5rem;
    flex: 1;
}

.content.checked .text{
    color: #6c567b;
    text-decoration: line-through;
    cursor: pointer;
    opacity: 0.6;
}